import React from 'react';
import { graphql } from 'gatsby';
import FormDemo from '../components/FormDemo';
import Layout from '../components/Layout';

const Form = ({ data }) => {
  return (
    <Layout pathPrefix={data.site.pathPrefix}>
      <FormDemo />
    </Layout>
  );
};

export default Form;

export const query = graphql`
  query FormQuery {
    site {
      pathPrefix
    }
  }
`;